/**
 *  Provider  value prop  context
 *  Provider(functional) + ProviderImpl 
 *  1.  context  <Provider><a/><b/></Provider>
 *  2. functional  provide inject 
 */

import { uniqueId, isPlainObject, defaults } from 'lodash'

const CommonProviderImpl = {
  name: 'veui-provider', // for better readability in devtools
  uiTypes: ['transparent'],
  props: {
    // eslint-disable-next-line vue/require-prop-types
    value: {}
  },
  render () {
    return this.$slots.default
  }
}

export function createContext (name, defaultValue) {
  const realName = name ? `${name}-provider` : CommonProviderImpl.name
  let contextId = `__${uniqueId(realName)}`
  let RealProviderImpl = {
    ...CommonProviderImpl,
    name: realName,
    inject: {
      [contextId]: {
        from: contextId,
        default: () => () => undefined
      }
    },
    provide () {
      return {
        [contextId]: () => {
          let parentContextValue = this[contextId]()
          if (
            isPlainObject(parentContextValue) &&
            isPlainObject(this.value) // provide  computed  provider  this.value
          ) {
            return defaults({}, this.value, parentContextValue) //  provider 
          }
          //  provider 
          return this.value
        }
      }
    }
  }

  let Provider = {
    functional: true,
    //  value prop context ProviderImpl
    render: (h, context) => wrapChildren(h, context, RealProviderImpl)
  }

  let useConsumer = (injectionKey) => {
    return {
      inject: {
        [contextId]: {
          from: contextId,
          default: () => () => undefined
        }
      },
      computed: {
        [injectionKey] () {
          if (defaultValue) {
            defaultValue =
              typeof defaultValue === 'function' ? defaultValue() : defaultValue
            //  context 
            return defaults({}, this[contextId](), defaultValue)
          }
          return this[contextId]()
        }
      }
    }
  }

  let Consumer = {
    ...useConsumer('context'),
    render () {
      return this.$scopedSlots.default(this.context)
    }
  }

  return { Provider, Consumer, useConsumer }
}

function wrapChildren (h, { data, children }, Provider) {
  const value = data.attrs ? data.attrs.value : undefined
  return children.map((child) =>
    h(
      Provider,
      {
        attrs: { value } // 
      },
      [child]
    )
  )
}
